import React from 'react';
import { Form, Field } from 'react-final-form';
import { RenderInput, RenderSelect, RenderCKeditor, RenderFile } from '../../../components';

export const EdPartieForm = ({
    edPartie = {},
    onSubmit = values => console.log(values),
    isAdd = false,
    children
}) => {

    const validate = (fields) => {
        const errors = {};
        if(!fields["phone"]) errors["phone"] = 'required';
        return errors;
    }

    return (
        <div className="edPartie-form">
            <Form
                onSubmit={onSubmit}
                validate={validate}
                initialValues={edPartie}
                render={({ handleSubmit }) => (
                    <form onSubmit={handleSubmit}>
                        <Field 
                            name="name" 
                            label="Name"
                            component={RenderInput}
                            placeholder="Name" 
                        />
                        <Field 
                            name="type" 
                            label="Type"
                            component={RenderSelect}
                            options={[
                                {
                                    label: 'teacher',
                                    value: 'teacher'
                                },
                                {
                                    label: 'school',
                                    value: 'school'
                                },
                                {
                                    label: 'institute',
                                    value: 'institute'
                                },
                            ]}
                            placeholder="Type" 
                        />
                        <Field 
                            name="phone" 
                            label="Phone"
                            type="mobile"
                            component={RenderInput}
                            placeholder="Phone" 
                        />
                        <Field 
                            name="address" 
                            label="address"
                            component={RenderInput}
                            placeholder="address" 
                        />
                        <Field 
                            name="latitude" 
                            label="latitude"
                            component={RenderInput}
                            placeholder="latitude" 
                        />
                        <Field 
                            name="longitude" 
                            label="longitude"
                            component={RenderInput}
                            placeholder="longitude" 
                        />
                        <Field 
                            name="country" 
                            label="country"
                            component={RenderInput}
                            placeholder="country" 
                        />
                        <Field 
                            name="city" 
                            label="city"
                            component={RenderInput}
                            placeholder="city" 
                        />
                        <Field 
                            name="description" 
                            label="description"
                            component={RenderCKeditor}
                            placeholder="description" 
                        />
                        <Field 
                            name="image" 
                            label="image"
                            component={RenderFile}
                            placeholder="image" 
                        />
                        {children}
                    </form>
                )}
            />
        </div>
    )
}
