import React, { useState, useEffect } from 'react';
import { Card, CardBody, CardHeader, Col, Row, Button } from 'reactstrap';
import PROFILE_PICTURE from '../../assets/img/profile-image.png';
import { getEdParties } from '../../handlers/Api/Calls';
import ReactTable from 'react-table-v6';
import { ErrorToast } from '../../components';
import { Link } from 'react-router-dom';
import { EdPartieActions } from './components/EdPartieActions';
import './edParties.scss';

const EdParties = props => {
  const [data, setData] = useState([]);
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    getEdParties((apiLoading, apiData, apiError) => {
      setData(apiData);
      setError(apiError);
      setIsLoading(apiLoading);
    });
  }, []);
  
  const handleAddEdPartie = (newEdPartie) => {    
    setData(prevEdParties => [newEdPartie, ...prevEdParties]);
  };
  
  const handleEditEdPartie = (editedEdPartie) => {    
    const edit = (arr, item) => {
      return arr.map(edPartie => {        
        if(edPartie.id === item.id) {
          edPartie = item;
        }
        return edPartie;
      });
    }
    setData(prevEdParties => edit(prevEdParties, editedEdPartie))
  }
  
  const handleDeleteEdPartie = (edPartieID) => {
    setData(prevEdParties => prevEdParties.filter(edPartie => {
      return edPartie.id !== edPartieID;
    }))
  }

  return (
    <div className="animated fadeIn">
      <Row>
        <Col xl={12}>
          {error && <ErrorToast error={error} />}
          <Card>
            <CardHeader>
              <i className="fa fa-align-justify"></i> EdParties <small className="text-muted">iEducate</small>
            </CardHeader>
            <CardBody>
              <div className="mb-2">
                <EdPartieActions 
                  showAdd
                  addCallback={handleAddEdPartie}
                />
              </div>

              <ReactTable
                data={data}
                loading={isLoading}
                defaultPageSize={10}
                className="-striped -highlight"
                filterable
                defaultFilterMethod={(filter, row) =>
                  !!String(row[filter.id]) && String(row[filter.id]).toLowerCase().includes(filter.value.toLowerCase())}
                columns={[
                  {
                    Header: "Basic",
                    columns: [
                      {
                        Header: "image",
                        accessor: "image",
                        width: 75,
                        Cell: row => <Link to={`/edParties/${row.original.id}`}>
                          <img 
                            src={row.value ? `data:image/png;base64,${row.value}` : PROFILE_PICTURE} 
                            width={40} 
                            height={40} 
                            style={{ borderRadius: 100 }}
                            alt="edPartie" 
                            className="d-block mx-auto"
                          />
                        </Link>,
                        filterable: false
                      },
                      {
                        Header: "name",
                        accessor: "name",
                      },
                      {
                        Header: "type",
                        accessor: "type",
                      }
                    ]
                  },
                  {
                    Header: "Info",
                    columns: [
                      {
                        Header: "address",
                        accessor: "address"
                      },
                      {
                        Header: "city",
                        accessor: "city"
                      },
                      {
                        Header: "country",
                        accessor: "country",
                      },
                      {
                        Header: "phone",
                        accessor: "phone",
                      },
                      {
                        Header: "location",
                        accessor: "latitude",
                        filterable: false,
                        width: 75,
                        Cell: row => {
                          return row.original.longitude && row.value ?
                            <Button 
                              target="_blank"
                              rel="noopener noreferrer"
                              color="info"
                              className="d-block"
                              href={`https://www.google.com/maps/search/?api=1&query=${row.value},${row.original.longitude}`}
                            ><i className="cui-location-pin"></i></Button>
                          : '';
                        }
                      },
                      {
                        Header: "actions",
                        accessor: "id",
                        filterable: false,
                        width: 150,
                        Cell: ({ original }) => (
                          <EdPartieActions 
                            showDelete 
                            showEdit 
                            edPartie={original} 
                            deleteCallback={handleDeleteEdPartie}
                            editCallback={handleEditEdPartie}
                          />
                        )
                      },
                    ]
                  }
                ]}
              />
            </CardBody>
          </Card>
        </Col>
      </Row>
    </div>
  )
}

export default EdParties;
